.container_panel_gifts{
    width: 100%;
    margin: auto;
}
.cpg_light{ background-color: #e0e0e0; }
.cpg_dark{ background-color: #212121; }
.container_gifs{
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 33%);
    grid-template-rows: auto;
    padding-bottom: 20px;
    justify-content: center;
}
.img_gif{
    border: 1px solid #9e9e9e;
    padding: 5px;
    margin: auto;
    margin-top: 20px;
    border-radius: 3px;
    width: 95%;
    height: 25vh;
    display: flex;
    justify-content: center;
}
.img_gif > img {
    max-width: 100%;
    max-height: 100%;
}
@media (max-width: 600px){
    .container_gifs{
        display: grid;
        grid-template-columns: repeat(1, 100%);
    }
}
@media(min-width: 601px) and (max-width: 992px){
    .container_gifs{
        display: grid;
        grid-template-columns: repeat(2, 45%);
    }
}
